<template>
    <template v-for="mode in props.modes" :key="mode">
        <IconButton
            v-if="mode === data.mode"
            :icon="modes[mode]?.icon"
            :title="`${t(modes[mode]?.title)}`"
            :class="{ invert: props.invert }"
            :disabled="props.disabled"
            class="size-m tertiary"
            @click="setMode(mode)"
        />
    </template>
</template>

<script setup>
    import { reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';

    const emit = defineEmits(['change']);
    const { t } = useI18n();

    const props = defineProps({
        code: { type: String, default: '' },
        modes: { type: Array, default: (['list', 'dashboard']) },
        mode: { type: String, default: 'list' },
        invert: { type: Boolean, default: false },
        disabled: { type: Boolean, default: false }
    });

    const modes = {
        list: {
            icon: 'preview-list',
            title: 'ui.view-mode.list'
        },
        dashboard: {
            icon: 'preview-dashboard',
            title: 'ui.view-mode.dashboard'
        },
        table: {
            icon: 'preview-table',
            title: 'ui.view-mode.table'
        }
    }

    const data = reactive({
        mode: localStorage[`view-mode-${props.code}`] || props.mode || props.modes[0]
    });

    const setMode = (mode) => {
        if (props.disabled) {
            return;
        }

        let index = props.modes.indexOf(mode) + 1;

        if (index === props.modes.length) {
            index = 0;
        }

        data.mode = props.modes[index];
        emit('change', data.mode);

        localStorage[`view-mode-${props.code}`] = data.mode;
    }
</script>

